import { RoadmapInterface } from "libs/interfaces"
import React, { useEffect, useRef, useState } from "react"

const HomeRoadmap = () => {
  const ref = useRef<HTMLDivElement>(null)

  const bubble1 = useRef<HTMLDivElement>(null)
  const bubble2 = useRef<HTMLDivElement>(null)
  const bubble3 = useRef<HTMLDivElement>(null)
  const bubble4 = useRef<HTMLDivElement>(null)

  const [offset, setOffset] = useState(0)
  const [line, setLine] = useState(0)
  const [active, setActive] = useState([false, false, false, false])

  const [roadmap] = useState<RoadmapInterface[]>([
    {
      title: "SO, IT BEGINS",
      sub: "The awakening",
      text: "Wing wars will be built from the grounds up, just like Rome. During this phase we will start recruiting soldiers for the Wing Wars army, these first soldiers will be the base of what will someday be the strongest community in the metaverse.",
    },
    {
      title: "STRETCH FOR THE SUMMIT",
      sub: "Conquest of the Metaverse",
      text: "We are not here to take part; we are here to take over. A lot of money will be spent to make sure everyone in crypto knows about us and the right people join. We will create a community of 100,000 people.",
    },
    {
      title: "THE ALMIGHTY SALE",
      sub: "Prepare your wallet",
      text: "Private and public mint will take place during this phase. Release date will be announced one week before mint, stay tuned... Check the benefits of owning a Wing Wars NFT below.",
    },
    {
      title: "THE ULTIMATE BATTLE",
      sub: "Prepare for war",
      text: "Start staking your NFTs to earn the Wing Wars token and wait for the most epic game in crypto gaming to be developed. The war is about to begin.",
    },
  ])

  useEffect(() => {
    handleScroll()
  }, [])

  useEffect(() => {
    window.addEventListener("scroll", handleScroll)

    return () => {
      window.removeEventListener("scroll", handleScroll)
    }
  }, [])

  useEffect(() => {
    setLine((old) => Math.max(old, -offset + window.innerHeight / 2))
  }, [offset])

  const handleScroll = () => {
    if (ref.current) {
      setOffset(ref.current.getBoundingClientRect().top)
    } else {
      setOffset(0)
    }

    onActive()
  }

  const onActive = () => {
    setActive((old) => old.map((entry: boolean, key: number) => (entry ? entry : onState(key))))
  }

  const onRef = (id: number) => {
    switch (id) {
      case 0:
        return bubble1
      case 1:
        return bubble2
      case 2:
        return bubble3
      case 3:
        return bubble4
      default:
        return bubble1
    }
  }

  const onState = (id: number) => {
    const element = onRef(id)

    if (element.current) {
      return element.current.getBoundingClientRect().top < window.innerHeight / 2
    }

    return false
  }

  return (
    <>
      <div id="#roadmap" className="w-full flex justify-center">
        <div className="w-[900px] max-w-full relative">
          <div className="absolute w-6 top-0 bottom-0 left-[16px] sm:left-[139px] md:left-[155px] lg:left-[195px] bg-[#3d3d3d] overflow-hidden">
            <div ref={ref} style={{ height: `${line}px` }} className="absolute top-0 w-6 bg-red overflow-hidden"></div>
            <div className="absolute top-0 bottom-0 right-0 w-6 ">
              <div className="absolute top-0 left-0 w-6 h-40 bg-gradient-to-b from-grey to-transparent"></div>
              <div className="absolute bottom-0 left-0 w-6 h-40 bg-gradient-to-t from-grey to-transparent"></div>
            </div>
          </div>
          <div className="w-full grid grid-cols-1 gap-40 sm:gap-60 md:gap-80 2xl:gap-[140px]">
            {roadmap.map((entry: RoadmapInterface, key: number) => (
              <div
                className="relative w-full grid grid-cols-[auto,1fr] sm:grid-cols-[auto,auto,1fr] gap-24 md:gap-40 lg:gap-80 py-40 lg:py-80"
                key={key}
              >
                <div className="hidden w-[80px] sm:w-[100px] sm:flex justify-end items-center">
                  <span
                    className={`text-24 md:text-36 lg:text-48 font-bold ${
                      active[key] ? "text-transparent bg-clip-text bg-gradient-to-r from-red to-blue" : "text-white"
                    }`}
                  >
                    {(key + 1) * 25}%
                  </span>
                </div>
                <div className="w-36 flex items-center">
                  <div
                    ref={onRef(key)}
                    className={`w-36 h-36 rounded-full flex items-center justify-center ${
                      active[key] ? "bg-gradient-to-r from-red to-blue" : "bg-[#3d3d3d]"
                    }`}
                  >
                    <div className="w-24 h-24 rounded-full bg-white"></div>
                  </div>
                </div>
                <div
                  className={`w-full grid grid-col-1 transition-all transform duration-500 ${
                    active[key] ? "opacity-1 translate-y-[0px]" : "opacity-0 translate-y-[-100px]"
                  }`}
                >
                  <div
                    className={`w-full mb-10 text-16 sm:text-18 font-black ${key % 2 === 0 ? "text-red" : "text-blue"}`}
                  >
                    {entry.title}
                  </div>
                  <div
                    className={`w-full mb-24 text-24 md:text-[36px] font-black ${
                      active[key] ? "text-white" : "text-white-60"
                    }`}
                  >
                    {entry.sub}
                  </div>
                  <div className="w-full font-regular text-white-60 text-14 sm:text-16 md:text-20">{entry.text}</div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
      <div className="w-full text-center text-36 sm:text-48 md:text-[64px] lg:text-96px font-black text-transparent bg-clip-text bg-gradient-to-b from-white-60 to-white-10">
        THE START OF A NEW ERA...
      </div>
    </>
  )
}

export default HomeRoadmap
