import Testament from "assets/img/testament.png"
import Bird1 from "assets/img/wings/1.jpg"
import Bird11 from "assets/img/wings/11.png"
import Bird12 from "assets/img/wings/12.png"
import Bird13 from "assets/img/wings/13.png"
import Bird14 from "assets/img/wings/14.png"
import Bird2 from "assets/img/wings/2.jpg"
import Bird3 from "assets/img/wings/3.jpg"
import Bird4 from "assets/img/wings/4.jpg"
import WorldBig from "assets/img/world_big.png"
import { SocialType } from "libs/enums"
import { openLink } from "libs/functions"
import { SocialInterface, TeamInterface } from "libs/interfaces"
import React, { useState } from "react"
import { FaInstagram, FaLinkedin, FaTwitter } from "react-icons/fa"

const HomeTeam = () => {
  const [team] = useState<TeamInterface[]>([
    {
      name: "FALCON",
      title: "Pulling the strings behind the scene. The north star of the Team. Project Manager & bird tamer.",
      avatar: Bird12,
      socials: [{ type: SocialType.Linkedin, link: "https://www.linkedin.com/in/i%C3%B1igo-peralta-57a313163/" }],
    },
    {
      name: "WOODPECKER",
      title: "The Architect, the Creator, the Oracle. Loving father of the birds. 3D Design Team leader.",
      avatar: Bird11,
      socials: [],
    },
    {
      name: "WISE OWL",
      title: "Born in the Metaverse, Satoshi's mate. Blockchain Guru.",
      avatar: Bird4,
      socials: [{ type: SocialType.Linkedin, link: "https://www.linkedin.com/in/andre-queiroz-costa-b5abb31b6" }],
    },
    {
      name: "GOLDEN EAGLE",
      title: "Elegant and well-looking. PR Team.",
      avatar: Bird3,
      socials: [{ type: SocialType.Linkedin, link: "https://www.linkedin.com/in/ignacio-vivas-hernando-99596b196/" }],
    },
    {
      name: "HAWK",
      title: "Silent, precise and deadly. 3D Design Team.",
      avatar: Bird1,
      socials: [],
    },
    {
      name: "THE DODO",
      title: "Dont mistake his cuteness. He is a dangerous bloke. 3D Design Team.",
      avatar: Bird2,
      socials: [],
    },
    {
      name: "CUCKOO",
      title: "No one really knows this guy. JK - Epic design skills. Game Designer.",
      avatar: Bird13,
      socials: [],
    },
    {
      name: "DONALD",
      title: "He looks like a duck. The God of social media. PR Team.",
      avatar: Bird14,
      socials: [],
    },
  ])

  return (
    <>
      <div className="w-full py-[200px] sm:py-[300px] md:py-[400px] relative">
        <img
          src={WorldBig}
          alt=""
          className="max-w-[5000px] max-h-[5000px] h-full absolute top-0 bottom-0 left-[50%] transform translate-x-[-50%]"
        />
        <div className="w-full relative grid grid-cols-1 gap-[60px] sm:gap-80 md:gap-100 lg:gap-[200px]">
          <div className="w-full flex justify-center">
            <img src={Testament} alt="" className="max-w-[820px] w-full" />
          </div>
        </div>
      </div>

      <div id="#team" className="w-full relative my-40 sm:my-60">
        <div className="w-full relative grid grid-cols-1 gap-40 md:gap-80 lg:gap-120">
          <div className="w-full flex justify-start">
            <div className="w-[530px] max-w-full grid grid-cols-1 gap-30">
              <div className="w-full text-white font-bold text-24 sm:text-36 md:text-42 lg:text-[48px] leading-[110%]">
                Meet the team
              </div>
              <div className="w-full text-14 sm:text-16 md:text-18 text-white-60">
                They trained, bred and enhanced us, the natural sons of Valhalla, the offspring of Mars, the hordes of
                Kalli. They took our killer instincts and amped them to eleven.
              </div>
            </div>
          </div>
          <div className="w-full justify-center">
            <div className="w-full max-w-full grid grid-cols-2 lg:grid-cols-4 gap-16 sm:gap-30 2xl:gap-x-70 2xl:gap-y-40">
              {team.map((person: TeamInterface, key: number) => (
                <div className="w-full" key={key}>
                  <div className="w-full flex justify-center mb-24">
                    <div className="w-[200px] max-w-full">
                      <img src={person.avatar} alt="" className="w-full rounded-full" />
                    </div>
                  </div>
                  <div className="w-full mb-16 text-center text-18 sm:text-24 text-white font-bold">{person.name}</div>
                  <div className="w-full text-center text-14 sm:text-18 font-regular text-white-60 mb-24">
                    {person.title}
                  </div>
                  <div className="w-full flex items-center justify-center gap-12">
                    {person.socials.map((social: SocialInterface, ke: number) => (
                      <button
                        onClick={(e) => openLink(e, social.link)}
                        className="w-50 h-50 flex group items-center justify-center rounded-full bg-white-10 hover:bg-white transition-all duration-300"
                        key={ke}
                      >
                        {
                          {
                            [SocialType.Instagram]: (
                              <FaInstagram className="transition-all duration-300 text-white group-hover:text-grey text-18" />
                            ),
                            [SocialType.Linkedin]: (
                              <FaLinkedin className="transition-all duration-300 text-white group-hover:text-grey text-18" />
                            ),
                            [SocialType.Twitter]: (
                              <FaTwitter className="transition-all duration-300 text-white group-hover:text-grey text-18" />
                            ),
                          }[social.type]
                        }
                      </button>
                    ))}
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default HomeTeam
