import { openLink } from "libs/functions"
import React from "react"

const FooterButton = ({ url, title }: { url: string; title: string }) => {
  return (
    <>
      <button onClick={(e) => openLink(e, url)} className="w-full group text-left">
        <span className="text-14 sm:text-16 md:text-18 font-bold text-white group-hover:text-transparent group-hover:bg-clip-text group-hover:bg-gradient-to-r group-hover:from-red group-hover:to-blue">
          {title}
        </span>
      </button>
    </>
  )
}

export default FooterButton
