import Banner from "assets/img/banner.png"
import BannerMobile from "assets/img/banner_mobile.png"
import { ReactComponent as OpenSea } from "assets/img/opensea.svg"
import Shadow from "assets/img/shadow.png"
import { SOCIAL } from "libs/constants"
import { openLink } from "libs/functions"
import React from "react"
import { FaDiscord } from "react-icons/fa"

const HomeTop = () => {
  return (
    <>
      <div id="#home" className="w-full min-h-[100vh] grid grid-cols-1 pt-[120px] sm:pt-[130px] xl:pt-[140px] relative">
        <div className="absolute w-screen bg-gradient-to-r from-blue to-red top-0 bottom-0 transform left-[50%] translate-x-[-50%]"></div>
        <div className="w-full grid grid-cols-1 relative">
          <div className="w-full flex justify-center items-start z-20">
            <div className="w-[640px] max-w-full grid grid-cols-1">
              <div className="w-full mb-16 sm:mb-30 text-24 sm:text-[48px] md:text-[64px] text-white text-center font-black leading-[110%]">
                The Rise of the Birds
              </div>
              <div className="w-full flex justify-center">
                <div className="w-[530px] max-w-full mb-20 sm:mb-30 text-14 sm:text-16 text-white-60 text-center">
                  Chickens and Pigeons vie for the absolute control of the planet, and the title of the deadliest
                  beasts.
                </div>
              </div>
              <div className="w-full flex justify-center">
                <div className="w-[124px] sm:w-[500px] max-w-full grid grid-cols-2 gap-12 sm:gap-24">
                  <button
                    onClick={(e) => openLink(e, SOCIAL.OPENSEA)}
                    className="h-[56px] sm:px-28 rounded-full flex items-center justify-center gap-12 bg-red"
                  >
                    <div className="w-22 h-22 rounded-full bg-white flex items-center justify-center">
                      <OpenSea className="fill-current text-red w-14 h-14" />
                    </div>
                    <span className="hidden sm:flex text-white font-bold text-14 md:text-18">Connect wallet</span>
                  </button>
                  <button
                    onClick={(e) => openLink(e, SOCIAL.DISCORD)}
                    className="h-[56px] rounded-full bg-blue flex items-center justify-center sm:px-28 gap-12"
                  >
                    <FaDiscord className="text-white" />
                    <span className="hidden sm:flex text-white font-bold text-14 md:text-18">Join discord</span>
                  </button>
                </div>
              </div>
            </div>
          </div>
          <div className="w-full aspect-w-16 aspect-h-6 relative">
            <img
              src={Banner}
              alt=""
              className="hidden sm:flex w-screen max-w-[100vw] h-auto absolute top-[100%] left-[50%] transform translate-x-[-50%] translate-y-[-99%]"
            />
            <img
              src={BannerMobile}
              alt=""
              className="flex sm:hidden w-screen max-w-[100vw] h-auto absolute top-[100%] left-[50%] transform translate-x-[-50%] translate-y-[-99%]"
            />
          </div>
        </div>
      </div>
      <div className="w-full relative">
        <img src={Shadow} alt="" className="w-[1900px] max-w-full" />
        <div className="absolute top-[50%] left-[50%] transform translate-x-[-50%] translate-y-[-50%] w-[66%] max-w-full">
          <div className="w-full text-16 md:text-24 xl:text-36 text-white text-center">
            After years of war and hunger, humans finally discovered what had always been in front of their eyes...
          </div>
        </div>
      </div>
    </>
  )
}

export default HomeTop
