import Icon from "assets/img/icon.png"
import Logo from "assets/img/logo.png"
import { ReactComponent as OpenSea } from "assets/img/opensea.svg"
import NavSocial from "components/nav/NavSocial"
import { SOCIAL, URL } from "libs/constants"
import { openLink } from "libs/functions"
import { LinkInterface } from "libs/interfaces"
import React, { useEffect, useState } from "react"
import { FaTwitter, FaDiscord, FaInstagram } from "react-icons/fa"
import { MdClose, MdKeyboardArrowRight, MdMenu } from "react-icons/md"
import { Link } from "react-router-dom"
import { Link as LinkScroll } from "react-scroll"
import Wrapper from "utils/wrapper/Wrapper"

const Nav = () => {
  const [open, setOpen] = useState(false)
  const [scrollPosition, setScrollPosition] = useState(0)

  const [links] = useState([
    { title: "Home", link: "#home" },
    { title: "Traits", link: "#traits" },
    { title: "Roadmap", link: "#roadmap" },
    { title: "Team", link: "#team" },
  ])

  const [socials] = useState([
    { title: "Twitter", icon: <FaTwitter className="text-grey text-18" />, link: SOCIAL.TWITTER },
    { title: "Discord", icon: <FaDiscord className="text-grey text-18" />, link: SOCIAL.DISCORD },
    { title: "Instagram", icon: <FaInstagram className="text-grey text-18" />, link: SOCIAL.INSTAGRAM },
  ])

  useEffect(() => {
    handleScroll()

    window.addEventListener("scroll", handleScroll, { passive: true })

    return () => {
      window.removeEventListener("scroll", handleScroll)
    }
  }, [])

  const handleScroll = () => {
    const position = window.pageYOffset
    setScrollPosition(position)
  }

  return (
    <>
      <div
        className={`fixed w-full z-40 min-w-[300px] h-80 lg:h-[100px] top-0 left-0 right-0 flex items-center transition ${
          scrollPosition > 0 ? "bg-white" : ""
        }`}
      >
        <div className="relative w-full flex justify-center px-20 sm:px-30">
          <div className="flex w-full max-w-screen-2xl flex-wrap gap-10 sm:gap-16 justify-between items-center">
            <Link to={URL.HOME} className="grid flow items-center justify-center gap-6">
              <img src={Logo} alt="" className="h-40 lg:h-60" />
            </Link>
            <div className="hidden lg:flex items-center justify-center gap-40">
              {links.map((link: LinkInterface, key: number) => (
                <LinkScroll
                  to={link.link}
                  offset={-120}
                  className="cursor-pointer text-18 text-grey font-bold"
                  key={key}
                >
                  {link.title}
                </LinkScroll>
              ))}
            </div>
            <div className="flex items-center justify-center gap-12 sm:gap-24">
              <div className="hidden w-full lg:grid grid-cols-4 justify-start gap-20">
                <NavSocial
                  url={SOCIAL.TWITTER}
                  icon={<FaTwitter className="text-22 text-grey group-hover:text-white transition-all duration-200" />}
                />
                <NavSocial
                  url={SOCIAL.DISCORD}
                  icon={<FaDiscord className="text-22 text-grey group-hover:text-white transition-all duration-200" />}
                />
                <NavSocial
                  url={SOCIAL.INSTAGRAM}
                  icon={
                    <FaInstagram className="text-22 text-grey group-hover:text-white transition-all duration-200" />
                  }
                />
                <NavSocial
                  url={SOCIAL.OPENSEA}
                  icon={
                    <OpenSea className="fill-current text-22 text-grey group-hover:text-white transition-all duration-200" />
                  }
                />
              </div>
              <button onClick={() => setOpen(!open)} className="w-40 h-40 flex lg:hidden justify-center items-center">
                <MdMenu className="text-grey text-28" />
              </button>
            </div>
          </div>
        </div>
      </div>
      <Wrapper open={open}>
        <div className="z-40 fixed min-w-[300px] top-0 left-0 w-full h-[100vh] bg-white-90 py-18">
          <div className="z-20 w-full grid grid-cols-1 relative">
            <div className="w-full flex justify-between items-center mb-20 px-20 md:px-30">
              <Link to={URL.HOME} className="grid flow items-center justify-center gap-6">
                <img src={Icon} alt="" className="h-40 lg:h-60" />
                <div className="hidden lg:flex font-bold text-white text-24">Ape Gorilla</div>
              </Link>
              <button
                onClick={() => setOpen(!open)}
                className="w-40 h-20 sm:h-30 md:h-40 flex justify-center items-center"
              >
                <MdClose className="text-grey text-32" />
              </button>
            </div>
            <div className="w-full flex items-center h-[calc(100vh-140px)] sm:h-[calc(100vh-160px)] lg:h-[calc(100vh-180px)] py-30">
              <div className="w-full grid grid-cols-1 items-center justify-center gap-4 max-h-full overflow-scroll">
                {links.map((link: LinkInterface, key: number) => (
                  <LinkScroll
                    to={link.link}
                    onClick={() => setOpen(!open)}
                    offset={-120}
                    className="w-full cursor-pointer flex items-center justify-between group font-bold px-20 md:px-30 py-12 hover:bg-[rgba(196,196,196,.2)]"
                    key={key}
                  >
                    <span className="text-18 font-bold text-grey group-hover:text-transparent group-hover:bg-clip-text group-hover:bg-gradient-to-r group-hover:from-red group-hover:to-blue">
                      {link.title}
                    </span>
                    <MdKeyboardArrowRight className="hidden group-hover:flex text-grey text-24" />
                  </LinkScroll>
                ))}
                {socials.map((link: LinkInterface, key: number) => (
                  <button
                    onClick={(e) => openLink(e, link.link)}
                    className="w-full flex items-center justify-between cursor-pointer group font-bold px-20 md:px-30 py-12 hover:bg-[rgba(196,196,196,.2)]"
                    key={key}
                  >
                    <span className="text-18 font-bold text-grey group-hover:text-transparent group-hover:bg-clip-text group-hover:bg-gradient-to-r group-hover:from-red group-hover:to-blue">
                      {link.title}
                    </span>
                    {link.icon}
                  </button>
                ))}
              </div>
            </div>
            <div className="w-full px-20 md:px-30">
              <div className="w-full text-grey-60 text-14 pt-20 sm:pt-30 border-t-1 border-grey-10">
                Copyright © Wing Wars {new Date().getFullYear()}
              </div>
            </div>
          </div>
        </div>
      </Wrapper>
    </>
  )
}

export default Nav
