import Icon from "assets/img/icon.png"
import { ReactComponent as OpenSea } from "assets/img/opensea.svg"
import FooterButton from "components/footer/FooterButton"
import FooterSocial from "components/footer/FooterSocial"
import { SOCIAL, URL } from "libs/constants"
import React from "react"
import { FaDiscord, FaInstagram, FaTwitter } from "react-icons/fa"
import { Link } from "react-router-dom"

const Footer = () => {
  return (
    <>
      <div className="w-full bg-green relative">
        <div className="w-full pb-30 sm:pb-40 pt-30 sm:pt-40 xl:pt-60 justify-center flex px-20 sm:px-30">
          <div className="w-full max-w-screen-2xl grid grid-cols-1 gap-20 sm:gap-30 md:gap-40">
            <div className="w-full grid grid-cols-1 xl:grid-cols-[1fr,auto] items-start gap-20 sm:gap-40 justify-between">
              <div className="w-full grid grid-cols-1 gap-30">
                <div className="w-full flex justify-start">
                  <Link to={URL.HOME} className="grid flow items-center justify-center gap-12">
                    <img src={Icon} alt="" className="h-30 sm:h-50" />
                  </Link>
                </div>
                <div className="w-full flex justify-start">
                  <div className="w-[400px] max-w-full text-14 sm:text-16 md:text-18 text-white-60">
                    Chickens and Pigeons vie for the absolute control of the planet, and the title of the deadliest
                    beasts.
                  </div>
                </div>
              </div>
              <div className="w-full flex items-start">
                <div className="grid grid-cols-1 xl:grid-cols-2 gap-40 sm:gap-60 xl:gap-80 items-start">
                  <div className="w-full flex items-start">
                    <div className="grid grid-cols-1 gap-30">
                      <div className="w-full font-bold text-24 text-white">Links</div>
                      <div className="w-[210px] grid grid-cols-1 gap-20">
                        <FooterButton url={URL.HOME} title={"Home"} />
                        <FooterButton url={SOCIAL.DISCORD} title={"Contact"} />
                        <FooterButton url={SOCIAL.PRIVACY} title={"Privacy Policy"} />
                        <FooterButton url={SOCIAL.TERMS} title={"Terms of Service"} />
                      </div>
                    </div>
                  </div>
                  <div className="w-full flex items-start">
                    <div className="grid grid-cols-1 gap-30">
                      <div className="w-full font-bold text-24 text-white">Our social media</div>
                      <div className="w-full grid grid-cols-4 justify-start gap-20">
                        <FooterSocial
                          url={SOCIAL.TWITTER}
                          icon={
                            <FaTwitter className="text-22 text-white group-hover:text-grey transition-all duration-200" />
                          }
                        />
                        <FooterSocial
                          url={SOCIAL.DISCORD}
                          icon={
                            <FaDiscord className="text-22 text-white group-hover:text-grey transition-all duration-200" />
                          }
                        />
                        <FooterSocial
                          url={SOCIAL.INSTAGRAM}
                          icon={
                            <FaInstagram className="text-22 text-white group-hover:text-grey transition-all duration-200" />
                          }
                        />
                        <FooterSocial
                          url={SOCIAL.OPENSEA}
                          icon={
                            <OpenSea className="fill-current text-22 text-white group-hover:text-grey transition-all duration-200" />
                          }
                        />
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="w-full border-b-1 border-white opacity-5"></div>
            <div className="w-full text-14 sm:text-16 md:text-18 text-white-60 text-center">
              Copyright © Wing Wars {new Date().getFullYear()}
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default Footer
