import Wanted from "assets/img/wanted.png"
import Bird1 from "assets/img/wings/1.jpg"
import Bird10 from "assets/img/wings/10.png"
import Bird11 from "assets/img/wings/11.png"
import Bird12 from "assets/img/wings/12.png"
import Bird13 from "assets/img/wings/13.png"
import Bird14 from "assets/img/wings/14.png"
import Bird15 from "assets/img/wings/15.png"
import Bird16 from "assets/img/wings/16.png"
import Bird2 from "assets/img/wings/2.jpg"
import Bird3 from "assets/img/wings/3.jpg"
import Bird4 from "assets/img/wings/4.jpg"
import Bird5 from "assets/img/wings/5.png"
import Bird6 from "assets/img/wings/6.png"
import Bird7 from "assets/img/wings/7.png"
import Bird8 from "assets/img/wings/8.png"
import Bird9 from "assets/img/wings/9.png"
import HomeSliderItem from "components/home/HomeSliderItem"
import { SOCIAL } from "libs/constants"
import { openLink } from "libs/functions"
import React, { useEffect, useState } from "react"
import { FaDiscord } from "react-icons/fa"

const HomeTopSlider = () => {
  const [birds1] = useState([Bird1, Bird9, Bird2, Bird10, Bird3, Bird11, Bird4, Bird12])
  const [birds2] = useState([Bird5, Bird13, Bird6, Bird14, Bird7, Bird15, Bird8, Bird16])

  const [offset, setOffset] = useState(0)

  useEffect(() => {
    handleScroll()

    window.addEventListener("scroll", handleScroll, { passive: true })

    return () => {
      window.removeEventListener("scroll", handleScroll)
    }
  }, [])

  const handleScroll = () => {
    const position = window.scrollY
    setOffset(position)
  }

  return (
    <>
      <div className="w-full flex justify-center">
        <div className="w-[590px] grid grid-cols-1">
          <div className="w-full mb-24 text-28 sm:text-36 md:text-[48px] text-white font-black text-center">
            That the greatest killers in nature are{" "}
            <span className="text-28 sm:text-36 md:text-[48px] text-red font-black">chickens</span> and{" "}
            <span className="text-28 sm:text-36 md:text-[48px] text-blue font-black">pigeons</span>
          </div>
          <div className="w-full mb-32 text-center text-14 sm:text-16 md:text-18 text-white-60">
            They trained, bred and enhanced us, the natural sons of Valhalla, the offspring of Mars, the hordes of
            Kalli. They took our killer instincts and amped them to eleven.
          </div>
          <div className="w-full mb-32 flex justify-center">
            <button
              onClick={(e) => openLink(e, SOCIAL.DISCORD)}
              className="h-[56px] rounded-full bg-blue flex items-center justify-center px-32 gap-12"
            >
              <FaDiscord className="text-white" />
              <span className="text-white font-bold">Join discord</span>
            </button>
          </div>
        </div>
      </div>
      <div className="w-full relative h-[220px] sm:h-[770px] mt-[60px]">
        <div className="hidden sm:flex absolute top-0 w-screen left-[50%] transform translate-x-[-50%] z-20">
          <div className="absolute top-[-80px] sm:top-0 w-full h-[200px] bg-gradient-to-b from-grey to-transparent"></div>
          <img
            src={Wanted}
            alt=""
            className="max-w-[210%] absolute top-0 left-[50%] transform translate-x-[-50%] translate-y-[-20%]"
          />
        </div>
        <div className="w-[calc(100vw+200px)] absolute top-[50%] left-[50%] transform translate-x-[-50%] translate-y-[-50%] mb-0 sm:mb-30 flex justify-center">
          <div className="w-full grid grid-cols-1 gap-10">
            <div className="w-full overflow-x-hidden h-180 sm:h-[250px] relative">
              <div
                style={{ left: `${-offset}px` }}
                className="absolute top-0 flex space-x-10 w-max transition duration-200"
              >
                {new Array(3).fill(1).map((entry: number, key: number) => (
                  <div className="flex space-x-10" key={key}>
                    {birds1.map((image: string, ke: number) => (
                      <HomeSliderItem image={image} key={ke} />
                    ))}
                  </div>
                ))}
              </div>
            </div>
            <div className="w-full overflow-x-hidden h-180 sm:h-[250px] relative">
              <div
                style={{ right: `${-offset}px` }}
                className="absolute top-0 flex space-x-[10px] w-max transition duration-200"
              >
                {new Array(3).fill(1).map((entry: number, key: number) => (
                  <div className="flex space-x-[10px]" key={key}>
                    {birds2.map((image: string, ke: number) => (
                      <HomeSliderItem image={image} key={ke} />
                    ))}
                  </div>
                ))}
              </div>
            </div>
            <div className="hidden sm:flex w-full overflow-x-hidden h-180 sm:h-[250px] relative">
              <div
                style={{ left: `${-offset}px` }}
                className="absolute top-0 flex space-x-[10px] w-max transition duration-200"
              >
                {new Array(3).fill(1).map((entry: number, key: number) => (
                  <div className="flex space-x-[10px]" key={key}>
                    {birds1.map((image: string, ke: number) => (
                      <HomeSliderItem image={image} key={ke} />
                    ))}
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default HomeTopSlider
